
*
{
  margin: 0px;
  padding: 0px;
}
.App {
  text-align: center;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.calculator
{
  position:absolute;
  top: 50px;
  right: 50px;
  left: 50px;
  margin-top: 150px;
  
}
.cal-btn
{
  padding: 20px 40px 20px 40px;
  font-size: 30px;
  background-color: #F0F0F0;
  display:inline-block;
  position: relative;
  top:10px;
  border: none;
  cursor: pointer;
  /* margin:10px ; */
}
.symboles
{
  color: #ef629f;
}
input
{
  position:relative;
  bottom: -12px;
  padding:60px 11.5px 60px 11.5px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  text-align: center;
  font-size: 30px;
  color: #F0F0F0;
  background-image: linear-gradient(#eecda3,#ef629f);
  border: 0px solid;
  outline: none;
}
.cal-btn:hover{
  color:#F0F0F0;
  background-color:#f6abb6;
}